.card {
  padding: 32px 16px 16px;
  border: 1px solid rgba(243, 243, 243, 1);
  border-radius: 5px;
  background-color: $bg-color;
  transition: transform 3s;

  &__title {
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
  }

  &:hover {
    transform: scale(120%);

    & .card__title {
      color: #34568b;
    }
  }

  &__image {
    display: block;
    margin: 0 auto 40px;
    width: 160px;
    height: 134px;
  }

  &__code {
    margin: 4px 0 0;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: $text-color;
  }

  &__stars {
    margin-top: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__review {
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 24px;

    &-title {
      font-size: 12px;
      font-weight: 400;
      line-height: 18px;
      color: $text-color;
    }

    &-value {
      font-size: 16px;
      font-weight: 700;
      line-height: 18px;
    }
  }

  &__button {
    margin-top: 16px;

    &-buy {
      display: flex;
      justify-content: center;
      align-items: center;
      padding-block: 12px;
      height: 40px;
      border-radius: 5px;
      text-transform: uppercase;
      background-color: $accent-color;
      border: none;
      text-decoration: none;
      font-size: 14px;
      font-weight: 700;
      line-height: 16px;
      color: $bg-color;
      transition: 3s;

      &:hover {
        border: 1px solid $accent-color;
        background-color: $bg-color;
        color: $accent-color;
      }
    }
  }
}
